body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-image {
    border-radius: 50%;
    height:200px;
    width:200px;
    margin: 20px 10px;
}
.background{
    max-width:100%;
    height:auto;
}
.center{
    margin:auto;
    position:relative;
    top:50px;
}
.jumbotron{
    background-color:#337AB7;
    color:white;
    margin: 0 auto;
    width:700px;
    font-family:'arial';
}

@media screen and (max-width: 600px){
    .jumbotron{
        width:400px;
        
    }
    .header-image{
        height:120px;
        width:120px;
    }
}

.footer{
    width: 100%;
    padding: 10px;
    margin-top: 20px;
    text-align: center;
    background-color: #3d3d3e;
    position: fixed;
    left: 0;
    bottom: 0;
  
}
.footer-text{
    font-family:arial;
    font-size: 15px;
    font-weight:600;
    color:#fff;
    /* text-transform:uppercase; */
}
img{
    height: 55px;
    width: 55px;
    border-radius:100%;
    margin: 0px 20px;
}
.media{
    display:block;
}

.card{
    margin: 20px auto;
    width:400px;
    height:200px;
  
}
